/****************/
/* Local Styles */
/****************/

header {
  background-color: #333;
  padding: 20px;
}
header nav {
  display: flex;
  flex-direction: row;
  align-items: center;
}
header h1, .fa-clock-o {
  color: #fff;
  margin: 0 0 0 10px;
  font-size: 2rem;
}

section {
  padding: 2rem;
}

:root {
  --display-background-color: rgb(245, 245, 245);
  --display-color: #222;
}

.display {
  align-items: center;
  background-color: var(--display-background-color);    
  display: flex;
  flex-direction: column;
  height: 10rem;
  justify-content: center;
  margin: auto;
  width: 16rem;
}

.display-time {
  border: none;
  color: var(--display-color);
  font-size: 24px;
  font-weight: 600;
  outline-width: 0;
  text-align: center;
}

.display-label {
  border: none;
  color: #999;
  font-size: 12px;
  font-weight: 600;
  margin: 0 2.3rem;
  text-align: center;
}

.display-input-group {
  padding: 0.2rem;
}

.display-time.display-hours {    
  border-right: none;
}

.display-time.display-minutes {    
  border-left: none;
  border-right: none;
}

.display-time.display-seconds {    
  border-left: none;
  border-right: none;
}

.display-time.display-milliseconds {    
  border-left: none;
}

.display-time.display-separator {  
  background: white;  
  border-left: none;
  border-right: none;
  padding-top: 0.59rem;
}

.display-time.form-control {
  font-size: 28px;
}

.display-countdown-time {
  color: #0082F7;
  font-size: 36px;
  font-weight: 600;
}

.display-countdown {
  align-items: center;
  background-color: var(--display-background-color);        
  display: flex;
  flex-direction: column;
  height: 20rem;
  justify-content: center;
  margin: auto;
  width: 16rem;
}

.display-countdown-label {
  border: none;
  color: #999;
  font-size: 12px;
  font-weight: 600;
  margin: 0 1.2rem;
  text-align: center;
}

.keypad {
  background-color: #999;
  padding: 0.2rem;
  height: 10rem;
}

.keypad-row {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: auto;
  width: 15.6rem;
}

.keypad-row > button {
  margin: 0.2rem;
  width: 100%;
}
.timer {
  align-items: center;
  border: #333 solid 0.2rem;
  background-color: #333;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 16.5rem;
}
.timer > div {
  width: 100%;
}
.controls {
  display: flex;
  flex-direction: row;
}
.controls > button {
  flex: 1;
  border-radius: 0;
  display: inline-block;
  width: auto;
}